.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.footer-logo {
  display: flex;
  align-items: center;
  gap: 20px;
}
.footer-logo p{
  color: #171717;
  font-size: 1.8rem;
  font-weight: 600;
  color: #FF4141;
  font-family: 'Merienda', cursive;
  font-family: 'Poppins', sans-serif;
}
.footer-logo p span {
  color: orange;
  font-size: 1.5rem;
  font-weight: 600;
  font-family: 'Merienda', cursive;
  font-family: 'Poppins', sans-serif;
}
.footer-links {
  display: flex;
  list-style: none;
  gap: 50px;
  color: #252525;
  font-size: 20px;
}
.footer-links li{
  cursor: pointer;
}
.footer-social-icons {
  display: flex;
  gap: 20px;
}
.footer-icon-container{
  padding: 10px;
  padding-bottom: 6px;
  background: #fbfbfb;
  border: 1px solid #ebebeb
}
.footer-copyright{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  width: 100%;
  margin-bottom: 30px;
  color: #1a1a1a;
  font-size: 20px;
}
.footer-copyright hr{
  width: 80%;
  border: none;
  border-radius: 10px;
  height: 3px;
  background: #c7c7c7;
}